.ifc_container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: white;
  min-height: fit-content;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  border-radius: 6px;
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  color: #1c023b;
  gap: 1vw;
  cursor: pointer;
  padding: 0.3rem 1rem;
  margin-top: 0.7rem;
}
.ifc_container > img {
  height: 2.5rem;
  width: 2.5rem;
}
.ifc_container_inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 0.2rem;
}
.ifc_container_inner > h6 {
  font-weight: 600;
  font-size: 0.9vw;
  line-height: 20px;
  color: #1c023b;
  margin: 0;
}
.ifc_container_inner > p {
  font-weight: 400;
  font-size: 0.75vw;
  line-height: 20px;
  color: rgba(18, 18, 18, 0.6);
  margin: 0;
}

.ifdl_heading {
  margin-top: 1rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  border: 0.5px solid #cacaca;
  padding-block: 0.5rem;
  padding-inline-start: 0.5rem;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}
.ifdl_heading > h6 {
  flex: 1 1;
  font-weight: 500;
  font-size: 0.9rem;
  color: #1c023b;
  margin: 0;
}
.ifdl_heading > h6:nth-child(2) {
  flex: 2 2;
}
.ifdl_heading > h6:nth-child(3),
.ifdl_heading > h6:nth-child(4),
.ifdl_heading > h6:nth-child(5) {
  flex: 0.7 0.7;
  text-align: center;
}
.ifdl_card {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  border: 0.5px solid #cacaca;
  border-top: none;
  padding-block: 0.5rem;
  padding-inline-start: 0.5rem;
}
.ifdl_card > h6 {
  flex: 1 1;
  font-weight: 500;
  font-size: 0.9rem;
  color: #1c023b;
  margin: 0;
  word-wrap: break-word;
}
.ifdl_card > img {
  flex: 1 1;
  margin: 0;
  height: 1.5rem;
  width: auto;
  cursor: pointer;
}
.ifdl_card > h6:nth-child(1) {
  max-width: 10rem;
}
.ifdl_card > h6:nth-child(2) {
  flex: 2 2;
}
.ifdl_card > img:nth-child(3),
.ifdl_card > img:nth-child(4),
.ifdl_card > img:nth-child(5) {
  flex: 0.7 0.7;
  text-align: center;
}
